import React from 'react'
import useTranslation from 'next-translate/useTranslation'

export default function Header() {
  const { t, lang } = useTranslation()

  const menu = [
    {
      label: t`common:home`,
      href: `/${lang}`,
    },
    {
      label: t`common:about-us`,
      href: `/${lang}/about-us`,
      children: [
        {
          label: t`common:what-do-we-do`,
          href: `/${lang}/about-us/what-do-we-do`,
        },
        {
          label: t`common:future-projects`,
          href: `/${lang}/about-us/future-projects`,
        },
      ],
    },
    {
      label: t`common:what-is-parkour`,
      href: `/${lang}/what-is-parkour`,
    },
    {
      label: t`common:participate`,
      href: `/${lang}/participate`,
    },
    {
      label: t`common:multimedia`,
      href: `/${lang}/multimedia`,
    },
    {
      label: t`common:contact`,
      href: `/${lang}/contact`,
    },
  ]

  return (
    <>
    <header>
      <nav className="menu">
        <ul>
          {menu.map(({ label, href, children }) => (
            <li key={label}>
              <a href={href}>{label}</a>
              {children && (
                <ul>
                  {children.map(d => (
                    <li key={label + '-' + d.label}>
                      <a href={d.href}>{d.label}</a>
                    </li>
                  ))}
                </ul>
              )}
            </li>
          ))}
        </ul>
      </nav>
    </header>

          <style jsx>
          {`
            header {
             background-color: #f9b53c;
             top: 0;
              left: 0;
              width: 100vw;
              position: fixed;
            }
            .menu ul {
              display: flex;
              justify-content: flex-end;
              padding: 0;
              margin: 0;
              background-color: transparent;
              list-style: none;
            }
            .menu a {
              display: block;
              padding: 2em;
              background-color: #f9b53c;
              transition-duration: 0.2s;
              text-decoration: none;
              color: #191c26;
            }
            .menu a:hover {
              background-color: #fac569;
            }
            .menu ul li ul {
              display: none;
            }
            .menu ul li a:hover + ul,
            .menu ul li ul:hover {
              display: block;
              max-width: 200px;
            }
          `}
        </style>
        </>
  )
}
